import html from "./template/pages/index.html"
import style from "./styles/main.scss";
import weather from './scripts/weather.js';

//getTags
const containerDiv = document.getElementById('timers_container')
const addTimerBtn = document.getElementById('addTimer')
const inputText = document.getElementById('inputText')
const clearAll = document.getElementById('clearAllTasks')
const timers_block_input = document.getElementById('inputText')
//variables
let minutes = 0;
let hours = 0;

const storedTimers = getFromLocalStorage('timers')
let timers = storedTimers || []; 

window.onload = function() {
  
  timers.map( (e)=> {
    if (e.isRunning == true ) {
      e.timerID =  setInterval(() => {
        if(!e.paused) {
          // e.seconds += Math.floor(Date.now() - e.startTime)/1000
        }
        e.seconds += Math.floor(Date.now() - e.startTime)/1000
          e.startTime = Date.now()
          setToLocalStorage(timers)
          showTimers()
        }, 1000);
    }
  })
  showTimers()

};


function getFromLocalStorage(data) {
  return JSON.parse(localStorage.getItem(`${data}`))
}

function setToLocalStorage(data) {
  return localStorage.setItem('timers', JSON.stringify(data))
}


function getTime(timestamp) {
  let hours = Math.floor(timestamp / 60 / 60);
  let minutes = Math.floor(timestamp / 60) - (hours * 60);
  let seconds = parseInt(timestamp % 60);


  let time = (hours ? (hours > 9 ? hours : "0" + hours) : "00") + ":" + (minutes ? (minutes > 9 ? minutes : "0" + minutes) : "00") + ":" + (seconds > 9 ? seconds : "0" + seconds);
  return time 
}
function output(data) {
let grafhicDiv = document.createElement('div')
grafhicDiv.classList.add('grapfh-container')

let grafhspan = document.createElement('div')
grafhspan.classList.add('grapfh-span')
grafhspan.setAttribute('job', 'done')

let grafhlastline = document.createElement('div')
grafhlastline.classList.add('grapfh-last-line')

let grafhline= document.createElement('div')
grafhline.classList.add('grapfh-line')

grafhicDiv.append(grafhspan)
grafhicDiv.append(grafhline)


let timerContainer = document.createElement('div')
timerContainer.classList.add('timerContainer')
timerContainer.setAttribute('id', data.itemID)

let timerSpan = document.createElement('div')
timerSpan.classList.add('timerSpan')

let textSpan = document.createElement('div')
textSpan.classList.add('textSpan')

let optionsDiv = document.createElement('div')
optionsDiv.classList.add('optionsDiv')

let playBtn = document.createElement('button')
playBtn.setAttribute('job', 'play')

let pauseBtn = document.createElement('button')
pauseBtn.setAttribute('job', 'pause')

let restartBtn = document.createElement('button')
restartBtn.setAttribute('job', 'restart')

let deleteBtn = document.createElement('button')
deleteBtn.setAttribute('job', 'delete')

optionsDiv.append(playBtn);
optionsDiv.append(pauseBtn)
optionsDiv.append(restartBtn)
optionsDiv.append(deleteBtn)


timerSpan.innerHTML = getTime(data.seconds)
textSpan.textContent = data.text

let timerBlock = document.createElement('div')
timerBlock.classList.add('timer-block')

timerBlock.append(grafhicDiv)

timerBlock.append(textSpan)
timerBlock.append(timerSpan)
timerBlock.append(optionsDiv)

timerContainer.append(timerBlock)

if(data.isRunning) {
  setInterval(() => {
  timerSpan.innerHTML = getTime(data.seconds)
 }, 1000);
}
if(data.done) {
  grafhspan.classList.add('done')

}
containerDiv.append(timerContainer)
}

function timerObj(itemID, text) {

  this.timerID = null,
  this.itemID = itemID, 
  this.isRunning = false,
  this.seconds = 0,
  this.text = text
  this.startTime = Date.now()
  this.paused = false
  this.done = false
}
function addTimer(event) {
  let text = inputText.value;
  let newTimer = new timerObj(timers.length, text)
  output(newTimer)
  timers.push(newTimer)
  setToLocalStorage(timers)
  inputText.value = ''

  // inputText.placeholder = 'Write a task...'
  }
  
  function showTimers() {
    containerDiv.textContent = ''

    for (let timer of timers) {
      output(timer)
    }
}
addTimerBtn.onclick = () => {
  console.log('wow')
  addTimer()
}

timers_block_input.addEventListener("keydown", (event)=> {
  if(event.keyCode === 13) {
    addTimer(event)
    console.log(event.target.value)
  }

})

function playTimer(currentTimer) {
  currentTimer.isRunning = true
  currentTimer.startTime = Date.now()

  currentTimer.timerID = setInterval(() => {
  currentTimer.seconds += (Math.floor((Date.now() - currentTimer.startTime)/1000))
  currentTimer.startTime = Date.now()
    setToLocalStorage(timers)
  }, 1000);
  showTimers()
}

clearAll.onclick = () => {
  let list = containerDiv.childNodes;
  list.forEach(element=> {
    element.innerHTML = ''
  })
    timers=[]
  setToLocalStorage(timers)
}

function deleteTimer(indexOfStoredTimer) {
  event.target.parentNode.parentNode.parentNode.remove()

  timers.splice(indexOfStoredTimer,1)
  setToLocalStorage(timers)

}

function restart(currentTimer) {
  currentTimer.seconds = 0
  setToLocalStorage(timers)
  showTimers()
}

function pause(currentTimer) {
  clearInterval(currentTimer.timerID)

  currentTimer.isRunning = false
  currentTimer.paused = true

  setToLocalStorage(timers)
}
function toggleDone(currentTimer, element) {
let sound = document.getElementById('sound');
console.log(sound)
  if(element.target.classList.contains('done')===true) {
    element.target.classList.remove('done')
    currentTimer.done = false
    setToLocalStorage(timers)
  }
  else if(!element.target.classList.contains('done')===true) {
    element.target.classList.add('done')
    
  // document.getElementById('sound').play()
    currentTimer.done = true
    setToLocalStorage(timers)
  }
}
